/* eslint-disable react-hooks/rules-of-hooks */
/* eslint-disable react/function-component-definition */
import React, { useCallback } from 'react';
import ShowCard from './ShowCard';
import NotFound from '../images/not-found.png';
import { FlexGrid } from '../styled';
import { useShows } from '../../misc/custom_hooks';

const ShowGrid = ({ Data }) => {
  const [starredShows, starredDispatch] = useShows();

  return (
    <FlexGrid>
      {Data.map(({ show }) => {
        const onStarClick = useCallback(
          (showId, isStarred) => {
            if (isStarred) {
              starredDispatch({ type: 'REMOVE', showId });
            } else {
              starredDispatch({ type: 'ADD', showId });
            }
          },
          // eslint-disable-next-line react-hooks/exhaustive-deps
          [starredDispatch]
        );

        return (
          <ShowCard
            key={show.id}
            id={show.id}
            name={show.name}
            image={show.image ? show.image.medium : NotFound}
            summary={show.summary}
            onStarClick={onStarClick}
            isStarred={starredShows.includes(show.id)}
          />
        );
      })}
    </FlexGrid>
  );
};

export default ShowGrid;
